import React, { Component } from 'react';
export default class About extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <section id="about">
         <div className="row">

            <div className="three columns">

               <img className="profile-pic"  src="images/portfolio/ProfilePicture.png" alt="" />

            </div>

            <div className="nine columns main-col">

               <h2>About Me</h2>
               <p>
               {
                 resumeData.aboutme
               }
               </p>

               <div className="row">

                  <div className="columns contact-details">

                  
                  <p className="scrolldown">
            <h2><a className="smoothscroll" href="#contact">Let's Connect<i className="icon-down-circle"></i></a></h2>
         </p>
                  {/* <p className="address">
       						<span>{resumeData.name}</span>
                     <br></br>
       						   <span>
                     {resumeData.address}
                    </span>
                    <br></br>
                    <span>{resumeData.website}</span>
       					   </p> */}
                  </div>
               </div>
            </div>
         </div>
      </section>
    );
  }
}